/* You can add global styles to this file, and also import other style files */
@use "@angular/material" as mat;

// @use "@tmdjr/ngx-editor-js2/styles/drag-preview.scss";
// @use "~@tmdjr/ngx-editor-js2/styles/drag-preview.scss";
@use "drag-preview.scss";

@use "styles/ngx-editor-js2-blockquotes-drag-preview.scss";
@use "styles/ngx-editor-js2-codemirror-drag-preview.scss";
@use "styles/ngx-editor-js2-pop-quiz-drag-preview.scss";
@use "styles/ngx-editor-js2-image-drag-preview.scss";
@use "styles/ngx-editor-js2-mfe-loader-drag-preview.scss";
@use "styles/ngx-editor-js2-mermaidjs-drag-preview.scss";
@use "styles/ngx-editor-js2-panel-drag-preview.scss";

// ! CODEMIRROR Dependencies
@use "codemirror/lib/codemirror";
@use "codemirror/theme/material-palenight";
// ! END CODEMIRROR Dependencies

html,
body {
  height: 100%;
}
body {
  margin: 0;
  font-family: Roboto, "Helvetica Neue", sans-serif;
}

body.light-mode {
  color-scheme: light;
  color: var(--mat-sys-on-surface);
  background-color: var(--mat-sys-surface);
}

body.dark-mode {
  color-scheme: dark;
  color: var(--mat-sys-on-surface);
  background-color: var(--mat-sys-surface);
}

body {
  &.red-palette {
    @include mat.theme(
      (
        color: mat.$red-palette,
      )
    );
  }

  &.green-palette {
    @include mat.theme(
      (
        color: mat.$green-palette,
      )
    );
  }

  &.blue-palette {
    @include mat.theme(
      (
        color: mat.$blue-palette,
      )
    );
  }

  &.yellow-palette {
    @include mat.theme(
      (
        color: mat.$yellow-palette,
      )
    );
  }

  &.cyan-palette {
    @include mat.theme(
      (
        color: mat.$cyan-palette,
      )
    );
  }

  &.magenta-palette {
    @include mat.theme(
      (
        color: mat.$magenta-palette,
      )
    );
  }

  &.orange-palette {
    @include mat.theme(
      (
        color: mat.$orange-palette,
      )
    );
  }
  &.chartreuse-palette {
    @include mat.theme(
      (
        color: mat.$chartreuse-palette,
      )
    );
  }
  &.spring-green-palette {
    @include mat.theme(
      (
        color: mat.$spring-green-palette,
      )
    );
  }
  &.azure-palette {
    @include mat.theme(
      (
        color: mat.$azure-palette,
      )
    );
  }
  &.violet-palette {
    @include mat.theme(
      (
        color: mat.$violet-palette,
      )
    );
  }
  &.rose-palette {
    @include mat.theme(
      (
        color: mat.$rose-palette,
      )
    );
  }
}

.flex-spacer {
  flex-grow: 1;
}

